$(document).ready(function () {
   $('.one-time').slick({
      infinite: false,
      speed: 400,
      slidesToShow: 5,
      slidesToScroll: 5,
      adaptiveHeight: true,
      arrows: true,
   });
});
$(document).ready(function () {
   $('.multiple-items').slick({
      infinite: false,
      speed: 400,
      slidesToShow: 3,
      slidesToScroll: 3,
      adaptiveHeight: true,
      arrows: true,
      nextArrow: '<img src="img/svg/arrow_right.svg" alt="" style="position: absolute; top: -50px; right: 0; cursor: pointer;">',
      prevArrow: '<img src="img/svg/arrow_left.svg" alt="" style="position: absolute; z-index: 10; top: -50px; left: 417px; cursor: pointer;">',
   });
});
$('.js-tab-trigger').on('click', function () {
   var tabName = $(this).data('tab'),
      tab = $('.js-tab-content[data-tab="' + tabName + '"]');

   $('.js-tab-trigger.active').removeClass('active');
   $(this).addClass('active');


   $('.js-tab-content.active').removeClass('active');
   tab.addClass('active');
});


